
@use 'base/variables' as va;
.Btn {
    width: 40px; /* Ajusta el ancho según sea necesario */
    height: 40px; /* Ajusta la altura según sea necesario */
    max-width: 10vw;
    max-height: 10vw;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    background-color: transparent;
    position: relative;
    border-radius: 7px;
    cursor: pointer;
    transition: all .3s;

    .svgContainer {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: transparent;
        backdrop-filter: blur(0px);
        letter-spacing: 0.8px;
        border-radius: 10px;
        transition: all .3s;
        border: 2px solid rgba(0, 0, 0, 0.466);
        z-index: 2;
        box-shadow: 0 0 10px $color_text_sombra;


        svg{
            width: 70%;
            height: 70%; 
        }
    }

    .BG {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%; 
        z-index: 1;
        border-radius: 10px;
        pointer-events: none;
        transition: all .3s;

        &.web{
            background: va.$color_web;
        }

        &.github{
            background: va.$color_github;
        }

        &.linkedin{
            background: va.$color_linkedin;
        }

        &.grabcad{
            background: va.$color_grabcad;
        }

        }
        
  }
  
    .Btn:hover .BG {
    transform: rotate(35deg);
    transform-origin: bottom;
    box-shadow: 0 0 10px $color_text_sombra;
    }
    
    .Btn:hover .svgContainer {
    background-color: rgba(156, 156, 156, 0.466);
    backdrop-filter: blur(4px);
    box-shadow: none;
    }